import {Album} from '../../src/models/Album.js';
import { Game } from '../../src/models/Game.js'
import { Collection } from '../../src/models/Collection.js'
import { Movie } from '../../src/models/Movie.js'
import test from 'ava';





    let album, collection, jeu, film

    test.beforeEach(t => {
        collection = new Collection()
        album = new Album("The Great War","19/07/2019","4.9/5","test/testImage.png","Sabaton",11)
        jeu = new Game("Mass Effect 3","6/03/2012","20/5","test/testImage.png","Bioware",1,"Cette boutique est ma préférée de la Citdelle")
        film = new Movie("Shutter Island","2010","4.7/5","test/testImage.png","Martin Scorsese","Leonardo Di Caprio, Mark Ruffalo","2h","Un île")

    })




    test ("ajout medias", t=> {
        collection.addMedia(album)
        collection.addMedia(jeu)
        collection.addMedia(film)

        t.is(collection.getNbMedias(),3)
        t.is(collection.medias,[album, jeu, film])
    })


    test ("suppression medias", t=> {
        collection.addMedia(album)
        collection.addMedia(jeu)
        collection.addMedia(film)

        collection.removeMedia(film)

        t.is(collection.getNbMedias(),2)
        t.deepEqual(collection.medias,[jeu, album])
    })

